
const API_KEY = "10923b261ba94d897ac6b81148314a3f";
const BASE_PATH = "https://api.themoviedb.org/3";


interface ITvShow {
    backdrop_path: string;
    poster_path:string;
    id:number;
    overview:string;
    name: string;
    vote_average:number;
    first_air_date:string;
    title: string; //tv title  
    //  
}


export interface ITvShowResult {
    page: number;
    results: ITvShow[];
    total_results: number;
    total_pages: number;
}





interface IMovie {
    backdrop_path: string;
    poster_path:string;
    title:string;
    overview:String;
    id:string;
    release_date:string;
    vote_average:number;
}


export interface IGetMoviesResult {//Api ()
    dates: {
        maximum:"string";
        minimum:"string";
    };
    page: number,
    results: IMovie[],//IMovie 
    total_pages: number,
    total_results: number,
}


//  
interface Isearch {
    backdrop_path: string;
    poster_path:string;
    title:string;
    overview:String;
    id:number;
    release_date:string;
    vote_average:number;
    original_title:string;
}


export interface IGetSearchResult {
    page: number,
    results: Isearch[],
    total_pages: number,
    total_results: number,
}


//API  


export interface IGetDetail {//api  
    runtime: number;
}

export function getMovies() {
    return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getPopular() {
    return fetch(`${BASE_PATH}/movie/popular?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}

export function getTop() {
    return fetch(`${BASE_PATH}/movie/top_rated?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}


export function getDetail(movieId: string) {
    return fetch(`${BASE_PATH}/movie/${movieId}?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}


export function getTvShow() {
    return fetch(`${BASE_PATH}/tv/popular?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}


export function getTopTvShow() {
    return fetch(`${BASE_PATH}/tv/top_rated?api_key=${API_KEY}`).then(
        (response) => response.json()
    );
}




// api

export function getSearchMovies(keyword: string) {
    return fetch(`${BASE_PATH}/search/movie?api_key=${API_KEY}&query=${keyword}`).then(
        (response) => response.json()
    );
}